@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

main {
    height: 100vh;
    .left {
        height: 90vh;
        display: flex;
        width: calc(100vw / 2);
        padding-left: 30px;
        flex-direction: column;
        float: left;

        .top {
            margin-top: 10px;
            .body {
                margin-left: 20px;
            }
        }

        .middle {
            margin-left: 30px;
            margin-top: 150px;
            h2 {
                margin-left: 30px;
            }

            .about {
                width: 500px;
                margin-left: 30px;
                color: #ffffff;
                font-family: 'Open Sans';
            }
        }

        .bottom {
            margin-top: 25vh;
        }
    }

    .right {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100vh;
        color: #08fdd8;
    }
}

@media screen and (max-width: 740px) {
    main {
        .left {
            .middle {
                margin-top: 60px;
                .about {
                    width: 300px;
                }
            }
        }
    }
}

@media screen and (max-width: 1322px) {
    main {
        .left {
            .middle {
                .about {
                    width: 200px;
                }
            }
        }

        .right {
            visibility: hidden;
            display: none;
        }
    }
}